export default {
  props: {
    mfzshow: {
      type: Boolean,
    }
  },
  data() {

    return {
      mfshow: false,
      ulst: true

    };
  },
  methods: {
    mozhuan() {
      let li = document.querySelector('#cube2')
      if (this.ulst) {
        li.style.transform = `rotateY(720deg) rotateX(720deg)`
        this.ulst = false
      } else {
        li.style.transform = ''
        this.ulst = true
      }

    }

  }
}



