  import '../../utils/jquery.min'
  export default {
    methods: {
      hasClassName(inElement, inClassName) {
        var classStr = inElement.className;
        var classArr = classStr.split(' ');
        for (var i = 0; i < classArr.length; i++) {
          if (inClassName == classArr[i]) {
            return true;
          }
        }
        return false;
      },

      addClassName(inElement, inClassName) {
        if (!this.hasClassName(inElement, inClassName))
          inElement.className = [inElement.className, inClassName].join(' ');
      },

      removeClassName(inElement, inClassName) {
        if (this.hasClassName(inElement, inClassName)) {
          var j = 0;
          var newClassArr = [];
          var classArr = inElement.className.split(' ');
          for (var i = 0; i < classArr.length; i++) {
            if (classArr[i] == inClassName) {
              continue;
            }
            newClassArr[j] = classArr[i];
            j++
          }

          inElement.className = newClassArr.join(' ');
//                第二种可以removeClass()
//                inElement.className = inElement.className.replace(inElement.className.match(inClassName),'');
        }
      },

      toggleClassName(inElement, inClassName) {
        if (this.hasClassName(inElement, inClassName))
          this.removeClassName(inElement, inClassName);
        else
          this.addClassName(inElement, inClassName);
      },

      toggleShape() {
        var shape = document.getElementById('shape');
        if (this.hasClassName(shape, 'ring')) {
          this.removeClassName(shape, 'ring');
          this.addClassName(shape, 'cube');
        } else {
          this.removeClassName(shape, 'cube');
          this.addClassName(shape, 'ring');
        }

        //转换样式translateZ（）导致的感觉太近
        var stage = document.getElementById('stage');
        if (this.hasClassName(shape, 'ring'))
          stage.style.webkitTransform = 'translateZ(-200px)';


        else
          stage.style.webkitTransform = '';

      },

      toggleBackfaces() {
        var backfacesVisible = document.getElementById('backfaces').checked;
        var shape = document.getElementById('shape');
        if (backfacesVisible)
          this.addClassName(shape, 'backfaces');
        else
          this.removeClassName(shape, 'backfaces');
      },

      Html5Audio(url, loop) {
        var audio = new Audio(url);
        audio.loop = loop;
        audio.autoplay = true;
        audio.play();
      },
    },
    created() {
      // this.Html5Audio('http://demo.daimabiji.com/558/Music/Fade.mp3', true)
    }
  }



