import {Notify, Dialog} from 'vant';

export default {
  components: {},
  data() {
    return {
      formdata2: {
        username: '',
        password: '',
        password2: ''
      },
      loginInfo: '',
      agree: true,
    }
  },
  methods: {
    goback() {
      this.$emit('hidlogin',false)
      this.$emit('hidrej',false)
      this.$store.commit('tar', true);

    },
    gologin() {
      this.$emit('hidlogin',true)
      this.$emit('hidrej',false)
      this.$store.commit('tar', false);
      setTimeout(() => {
        this.formdata2.username = '';
        this.formdata2.password = '';
        this.formdata2.password2 = '';
      }, 200);
    },
    //点击注册
    reject() {
      if (!this.formdata2.username) {
        Notify({
          message: '账号不能为空!',
          duration: 1500

        });
      } else {
        if (!this.formdata2.password) {
          Notify({
            message: '密码不能为空!',
            duration: 1500

          });
        } else {
          if(this.formdata2.password===this.formdata2.password2){
            this.$axios.post('admin/add',
              {username: this.formdata2.username, password: this.formdata2.password}).then(res => {
              if (res.code === 400) {
                Dialog.alert({
                  message: '前头有个家伙已经抢先注册了',
                  confirmButtonColor: '#f54882',
                })
              } else if (res.code === 200) {
                Notify({
                  message: '注册成功！',
                  background: '#409EFF',
                  duration: 2000,
                });
                setTimeout(() => {
                  this.$emit('hidlogin',true);
                  this.$emit('hidrej',false);
                  this.formdata2.username = '';
                  this.formdata2.password = '';
                  this.formdata2.password2 = ''
                }, 1000)
              }
            })
          }else {
            Notify({
              message: '两次输入的密码不一致',
            });
          }

        }
      }
    },


  },


}



