import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex);
const store = new Vuex.Store({
  state: {
    tarbr:true,
    logindata: localStorage.getItem('logininfo'),
    username: localStorage.getItem('username'),
    password:localStorage.getItem('password'),
    token: localStorage.getItem('token'),
    avatar: localStorage.getItem('avatar'),
    user:localStorage.getItem('user')?JSON.parse(localStorage.getItem('user')):'',
    ilove:localStorage.getItem('ilove')?JSON.parse(localStorage.getItem('ilove')):'',
    loveid:localStorage.getItem('iloveid'),
    // manid:'wancomic'
    // manid:'kucomic'
    manid:'qicomic',
    point:107,
    his:localStorage.getItem('his')?JSON.parse(localStorage.getItem('his')):[],
    lianai:'',
    footid:0,
    allbook:localStorage.getItem('allbook')?JSON.parse(localStorage.getItem('allbook')):''

  },
  mutations: {
    setallbook(state,payload){
      state.allbook=payload;
      let jsonstr=JSON.stringify(payload)
      localStorage.setItem('allbook', jsonstr)
    },
    setfoot(state,payload){
      state.footid=payload;
    },
    lianai(state,payload){
      state.lianai=payload;
    },
    setmanid(state,payload){
      state.manid=payload;
    },
    set_iloveid(state, payload){
      state.ilove=payload;
      localStorage.setItem('iloveid', payload)
    },
    //保存收藏
    set_ilove(state, payload){
      state.ilove=payload;
      let jsonstr=JSON.stringify(payload)
      localStorage.setItem('ilove', jsonstr)
    },
    //保存用户是否在登录状态中
    CHANGE_LOGININFO(state, payload) {
      state.logindata = payload;
      localStorage.setItem('logininfo', payload)
    },
    //保存用户名状态
    KEEP_USERNAME(state, payload) {
      state.username = payload;
      localStorage.setItem('username', payload)
    },
    //浏览器保存密码
    KEEP_PASSWORD(state, payload){
      state.password = payload;
      localStorage.setItem('password', payload)
    },
    //上传用户自定义头像
    keeptavatar(state, payload) {
      state.avatar = payload;
      localStorage.setItem('avatar', payload)
    },

    //保存token状态
    settoken(state, payload) {
      state.token = payload;
      localStorage.setItem('token', payload)
    },
    //注销操作删除登录信息
    loginout(state, payload) {
      state.token = payload;
      state.logindata = payload;
      state.avatar = payload;
      state.user = payload;
      state.ilove=payload;
      localStorage.setItem('token', payload)
      localStorage.setItem('logininfo', payload)
      localStorage.setItem('avatar', payload)
      localStorage.setItem('user', payload)
      localStorage.setItem('ilove', payload)
    },
    keepuser(state, payload){
      state.user = payload;
      let jsonstr=JSON.stringify(payload)
      localStorage.setItem('user', jsonstr)
    },
    tar(state,payload){
      state.tarbr = payload;
    },
    //设置历史浏览记录
    set_his(state,payload){
      state.his=payload;
      localStorage.setItem('his',JSON.stringify(payload) )
    }
  },

});
export default store



