import Vue from 'vue'
import Router from 'vue-router'
import Index from "@/views/index"
import Person from '@/views/person'
import Zfish from '@/views/zfish'
import Test from '@/views/zfish/test'
import Testlast from '@/views/zfish/testlast'
import Search from '@/views/search'
import Detali from '@/views/detail'
import Ilove from '@/views/ilove'
import Message from '@/views/message'
import Catogea from '@/views/indexpro/category'
Vue.use(Router);
export default new Router({
  // mode:'history',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Index,
      meta: {keepAlive: true, scrollTop: 0,index:1}
    },
    {
      path: '/person',
      name: 'Person',
      component: Person,
      meta: {keepAlive: true, scrollTop: 0,index:1.3}
    },

    {
      path: '/zfish',
      name: 'Zfish',
      component: Zfish,
    },
    {
      path: '/test',
      name: 'Test',
      component: Test,
      meta: {keepAlive: true, scrollTop: 0,index:2}

    },
    {
      path: '/testlast',
      name: 'Testlast',
      component: Testlast
    },

    {
      path: '/search',
      name: 'Search',
      component: Search,
      meta: {keepAlive: true, scrollTop: 0,index:2}

    },
    {
      path: '/detail/:id',
      name: 'Detail',
      component: Detali,
      meta: {index:3}
    },
    {
      path: '/ilove/:id',
      name: 'Ilove',
      component: Ilove,
      meta: {keepAlive: true, scrollTop: 0,index:1.2}
    },
    {
      path: '/category',
      name: 'Catogea',
      component: Catogea,
      meta: {keepAlive: true, scrollTop: 0,index:1.1}
    },

    {
      path: '/message',
      name: 'Message',
      component: Message,
      meta: {keepAlive: true, scrollTop: 0,index:2}
    },
  ]
})



