import loading from '../../components/loading'
import mofang from '../../components/mofang'
import footline from '../../components/footline'
import rp from "request-promise"
import cheerio from "cheerio"

export default {
  components: {loading, mofang, footline},
  data() {
    return {
      // mfzshow: true,
      loadingshow: false,
      searchempty: false,
      searshow: false,
      aaa: false,
      vanpeosear: true,
      comicinput: sessionStorage.getItem('comicinput'),
      searchinfo: [],
      hostlist: [
        {
          "name": "我为苍生",
          "url": '14294',
        },
        {
          "name": "超科学大脑研究部",
          "url": '15322',
        },
        {
          "name": "银之守墓人",
          "url": '16040',
        },
        {
          "name": "活城",
          "url": '13102',
        },
        {
          "name": "被青梅竹马攻略了怎么办",
          "url": '12514',
        },
        {
          "name": "据说我是王的女儿?",
          "url": '16485',
        },
        {
          "name": "一人之下",
          "url": '16080',
        },
        {
          "name": "血族禁域 ",
          "url": '14386',
        },
        {
          "name": "从前有座灵剑山 ",
          "url": '21127',
        },


      ],
      booklist: this.$store.state.allbook
    }
  },
  methods: {
    hotitem(url) {
      this.$router.push(`/detail/${url}`);
    },
    back() {
      this.$router.back(-1);
    },
    clearval() {
      this.searshow = false;
      this.comicinput = ""
      sessionStorage.setItem('comicinput', '')
      setTimeout(() => {
        this.searchinfo = ''
      }, 500)

    },
    //点击搜索
    clickcearch() {
      if (this.comicinput) {
        this.searchinfo = []
        sessionStorage.setItem('comicinput', this.comicinput)
        this.loadingshow = true;
        this.searshow = true;
        let reg = new RegExp(this.comicinput);
        this.booklist.map(item => {
          if (item.title.match(reg)) {
            this.searchinfo.push(item)
          }
        })
        rp(`http://m.qiman6.com/spotlight?keyword=${this.comicinput}`).then(body => {
          let $ = cheerio.load(body);
          let data = [];
          let box = $('.search-result .comic-list-item');
          if (box.length) {
            for (let i = 0; i < box.length; i++) {
              data.push({
                id: box[i].attribs['data-cid'],
                title: box[i].children[3].children[1].children[0].children[0].data,
                img: box[i].children[1].children[0].attribs.src,
                last: box[i].children[3].children[7]?box[i].children[3].children[7].children[0].data:box[i].children[3].children[4].children[3].children[0].data,
                auto: box[i].children[3].children[3].children[0] ? box[i].children[3].children[3].children[0].data : '佚名',
                sl: box[i].children[3].children[5]?box[i].children[3].children[5].children[1].children[0].data:box[i].children[3].children[4].children[1].children[1].children[0].data,
              })
            }
            this.searchinfo = this.searchinfo.concat(data);
            this.searchempty = false
            this.loadingshow = false;
          } else {
            this.loadingshow = false
            this.searchempty = true
          }
        }).catch(err => {
          this.loadingshow = false
          this.searchempty = true
          console.log(err);
        })
      }
    },
    gocomicdetail(url) {
      this.$router.push(`/detail/${url}`)
    },
    changemf() {
      // this.mfzshow = !this.mfzshow
    },
  },
  activated() {
    mui.plusReady(function () {
      plus.navigator.setStatusBarStyle('light');
    });
    if (this.searchinfo.length) {
      this.vanpeosear = true;
      this.searshow = false;
      setTimeout(() => {
        this.searshow = true
      }, 350)
    } else {
      this.vanpeosear = false;
      this.searshow = false;
      setTimeout(() => {
        this.vanpeosear = true
      }, 250);
    }
  },
  mounted(){
    mui.plusReady(() => {
       this.$mui.back = () => {
         this.$router.go(-1);
         };
       });
  }
}



