export default {
  data() {
    return {
      isLoading: false,
      list: '',
      loading: true,
      finished: false,
      errorpic: '../../assets/error.jpg',
      num: 2
    }
  },
  methods: {

    getlist() {
      this.$axios.get('kucomic/rank').then(res => {
        this.list = res.data;
        this.isLoading = false;
        this.loading = false;
        this.finished = true;
        setTimeout(() => {
          let N = document.querySelectorAll('.pronum-text')
          let n1 = document.querySelectorAll('.pronum-text')[0]
          let n2 = document.querySelectorAll('.pronum-text')[1]
          let n3 = document.querySelectorAll('.pronum-text')[2]
          for (let i = 0; i < 3; i++) {
            N[i].style.width = 0.68 + 'rem'
            N[i].style.height = 0.7 + 'rem'
            N[i].style.borderRadius = 0;
            N[i].style.backgroundColor = `#fff`;
            N[i].innerHTML = '';
          }
          n1.style.backgroundImage = `url(${require('../../assets/taber/icon_ranking_1.png')})`;
          n2.style.backgroundImage = `url(${require('../../assets/taber/icon_ranking_2.png')})`;
          n3.style.backgroundImage = `url(${require('../../assets/taber/icon_ranking_3.png')})`;
        }, 0)

      })

    },
    godetail(item) {
      this.$router.push(`/detail/${item.id}`)
    },
  },
  created() {
    this.getlist();
  },

}



