import footers from '../../components/footer'
import request from "request"
import cheerio from "cheerio"
import footline from '../../components/footline'
import loding from '../../components/loading'
export default {
  components: { footline,loding,footers},
  data() {
    return {
      totopshow: false,
      url: '',
      Footerinfo: 0,
      point: '',
      list: '',
      listdata: {six: [], four: []},
      Fine: []
    }
  },
  methods: {
    chuck(arr, len) {
      let arrLength = arr.length
      let result = []
      for (let i = 0; i < arrLength; i += len) {
        result.push(arr.slice(i, i + len))
      }
      return result
    },
    goback() {
      this.$router.back(-1)
    },
    addImg(e) {
      let inputDOM = this.$refs.inputer.files[0];
      let imgid = new FormData();
      imgid.append("file", 'multipart');
      imgid.append("Filedata", inputDOM);
      // imgid.append('file', inputDOM);
      this.$axios.post('https://sm.ms/api/v2/upload?inajax=1',	imgid).then(res=>{
        console.log(res);
      })},
    // this.$axios.post('https://api.uomg.com/api/image.360',	imgid).then(res=>{
    //     console.log(res.imgurl);
    //   })},
    goreq() {


    },
    goaxios() {

    },
  },

}



