
import {Dialog, Toast} from 'vant';
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Divider from 'vux/src/components/divider/index.vue'

import Heizi from '../../components/heizPowndown'

export default {
components: {Heizi, Tab, TabItem, Divider},
data() {
return {
current: 0,

logintime: '',
axioscode: 0,
codedata: '',
ilove: [],
swpindex2: 1,
swiperOption: {
loop: false, //开启循环模式
speed: 500,//轮播速度,
initialSlide: 1
},

delgo: false,
checkedNames: [],
all_cheched: false,
hislist: ''
};
},
methods: {
changeTime(time) {
if (time) {
let getBz = function (num) {
if (parseInt(num) < 10) {
num = '0' + num;
}
return num;
};
let oDate = new Date(time * 1),
oMonth = oDate.getMonth() + 1,
oDay = oDate.getDate(),
oHour = oDate.getHours(),
oMin = oDate.getMinutes(),
oSen = oDate.getSeconds(),
oTime = getBz(oMonth) + '-' + getBz(oDay) + ' ' + getBz(oHour) + ':' + getBz(oMin) + ':' + getBz(oSen);
return oTime;
} else {
return "";
}

},

nolove() {
if (this.checkedNames.length) {
let checkedNames = this.checkedNames;
let ilovelist = this.ilove;
Dialog.confirm({
title: '确定不再喜欢这些作品吗?',
confirmButtonColor: '#fd7c51',
confirmButtonText: '是的',
cancelButtonColor: '#aaa',
cancelButtonText: '再想想',
width: '6.5rem',
className: 'ilove-dialog'
}).then(() => {
checkedNames.forEach((item, index) => {
ilovelist.forEach((itemlove, i) => {
if (itemlove.id === item) {
ilovelist.splice(i, 1);
}
});
});
this.$axios.post('admin/ilove',
{
username: this.$store.state.username,
password: this.$store.state.password,
ilove: ilovelist
}).then(res => {
if (res.code === 200) {
this.$store.commit('set_ilove', ilovelist);
Toast({message: '成功取消收藏(ΩДΩ)', duration: 1500})
this.godele()
}
})
}).catch(() => {
});

}

},

allChecked() {

if (this.checkedNames.length === this.ilove.length) {

this.checkedNames = [];
} else {
this.checkedNames = [];

this.ilove.forEach((item) => {
this.checkedNames.push(item.id); 
})
}
},


gologin() {
this.$router.push('/person')
},

readon(item) {

},
swperchange() {
if (this.delgo) {
this.godele()
}
let line = document.querySelector('#tttb').childNodes[0].childNodes[0].childNodes[4]
if (this.$refs.loveswper.swiper.activeIndex === 0) {
this.swpindex2 = 0;
setTimeout(res => {
line.style.left = `15%`;
line.style.right = `65%`
})
} else {
this.swpindex2 = 1;
setTimeout(res => {
line.style.left = `65%`;
line.style.right = `15%`
})

}
},
puwngetdata() {
this.$axios.post('admin/login',
{username: this.$store.state.username, password: this.$store.state.password}).then(res => {
if (res.code === 200) {
this.$store.commit('keeptavatar', res.data.avatar);
this.$store.commit('keepuser', res.data);
this.$store.commit('set_ilove', res.data.ilove);
this.ilove = this.$store.state.ilove
this.axioscode = 200;
setTimeout(() => {
this.axioscode = 0;
}, 1000)
} else {
this.axioscode = 400;
}
})

},
hispull() {





},
hisdetail(id) {

this.$router.push(`/detail/${id}`)
},
gohis() {

this.$refs.loveswper.swiper.slideTo(1, 300, false);
},
golove() {
this.$refs.loveswper.swiper.slideTo(0, 300, false);
},
godetail(i, e) {
if (!this.delgo) {
this.$router.push(`/detail/${e.id}`)
}
},

godele() {
this.checkedNames = []
let check = document.querySelector('.ilove-all-check-box');
let godel = document.querySelector('.van-icon-qr');
if (this.delgo) {
godel.style.transform = `rotate(0deg)`
godel.style.color = `#009eec`;
setTimeout(res => {
this.$store.commit('tar', true);
}, 100)
if (check) {
check.style.bottom = -1.2 + 'rem';
}
this.delgo = false
} else {
this.$store.commit('tar', false);
godel.style.transform = `rotate(-145deg)`
godel.style.color = `#ef0403`
setTimeout(res => {
check.style.bottom = 0;
}, 100)
this.delgo = true
}

},
gogo() {
let line = document.querySelector('.vux-tab-ink-bar')
line.style.left = `15%`
line.style.right = `65%`
},
hisgogo() {






}
},
activated() {
mui.plusReady(function () {
plus.navigator.setStatusBarStyle('dark');
});
setTimeout(res => {
this.ilove = this.$store.state.ilove;
this.hislist = this.$store.state.his
if (this.$route.params.id * 1 === 2) {
setTimeout(res => {
this.$refs.loveswper.swiper.slideTo(1, 500, false);
}, 300)
} else {
setTimeout(res => {
this.$refs.loveswper.swiper.slideTo(0, 500, false);
}, 300)
}
})

},
beforeRouteLeave(to, from, next) {
this.$refs.heiziPowdn.btscroll.destroy();
next()
},
};



