import {Toast} from 'vant';
import footend from '../../components/footline'
import Heizi from '../../components/heizPowndown'
export default {
  components: {footend, Heizi},
  data() {
    return {
      msgtext: '',
      message: '',
      msglist: '',
      ifback: false,
      backid: '',
      baindex: '',
      axioscode: 0
    }
  },
  methods: {
    puwngetdata() {
      this.$axios.get('adminserver/getmsg').then(res => {
        if (res.code === 200) {
          this.msglist = res.data;
          this.axioscode = 200;
          setTimeout(res => {
            this.axioscode = 0;
          }, 1000)
        } else {
          this.axioscode = 400;
        }
      })

    },

    back() {
      this.$router.back('-1')
    },
    //发送
    gomsg() {
      if (this.msgtext) {
        //回复
        let time = new Date().getTime();
        if (this.ifback) {
          let name='游客'
          if (this.$store.state.logindata) {
            if(this.$store.state.user.nickname){
              name = this.$store.state.user.nickname
            }else {
              name = this.$store.state.username
            }
          }
          let reblist = this.msglist[this.baindex].remsg;
          reblist.push({
            name, time, message: this.msgtext
          });
          this.$axios.post('adminserver/remsg', {id: this.backid, remsg: reblist}).then(res => {
            if (res.code === 200) {
              Toast.success({duration: 1000, message: '回复成功！'});
            } else {
              Toast.fail({duration: 1000, message: '回复失败'});
            }
            this.goinput()
          })
        } else {
          if (this.msgtext) {
            let name='游客'
            let user;
              if (this.$store.state.logindata) {
              user=this.$store.state.username;
              if(this.$store.state.user.nickname){
                name = this.$store.state.user.nickname
              }else {
                name = this.$store.state.username
              }
            }
            let avatar = this.$store.state.logindata ? this.$store.state.avatar : 'http://upload.ouliu.net/i/20200309185331ufp74.png';
            this.$axios.post('adminserver/addmsg', {name, message: this.msgtext, time, avatar,user}).then(res => {
              if (res.code === 200) {
                Toast.success({duration: 1000, message: '留言成功！'});
                this.$axios.get('adminserver/getmsg').then(res => {
                  this.msglist = res.data;
                });
              } else {
                Toast.fail({duration: 1000, message: '留言失败'});
              }
              this.goinput()
            })
          }
        }
      } else {
        Toast.fail({duration: 1500, message: '留言内容不能为空！'})
      }
    },
    goinput(reb, name) {
      let inbox = document.querySelector('.msg-content');
      let back = document.querySelector('.black-back');
      let arrea = document.querySelector('textarea');
      if (name) {
        arrea.placeholder = `回复@${name}`
        this.ifback = true;
      } else {
        arrea.placeholder = '请输入留言内容(150个字以内)'
        this.ifback = false;
      }
      if (inbox.style.display === 'none') {
        inbox.style.display = 'block';
        document.querySelector('textarea').focus();
        setTimeout(res => {
          back.style.backgroundColor = 'rgba(0,0,0,0.3)';
        }, 200)
      } else {
        setTimeout(res => {
          back.style.backgroundColor = 'rgba(104,104,104,0)';
        }, 200)
        inbox.style.display = 'none';
        this.msgtext = ''
      }
    },
    timeback(dateTimeStamp) {
      var result = ''
      var minute = 1000 * 60
      var hour = minute * 60
      var day = hour * 24
      var month = day * 30
      var now = new Date().getTime()
      var diffValue = now - dateTimeStamp
      if (diffValue < 0) return
      var monthC = diffValue / month
      var weekC = diffValue / (7 * day)
      var dayC = diffValue / day
      var hourC = diffValue / hour
      var minC = diffValue / minute
      if (monthC >= 1) {
        result = "" + parseInt(monthC) + "月前"
      }
      else if (weekC >= 1) {
        result = "" + parseInt(weekC) + "周前"
      }
      else if (dayC >= 1) {
        result = "" + parseInt(dayC) + "天前"
      }
      else if (hourC >= 1) {
        result = "" + parseInt(hourC) + "小时前"
      }
      else if (minC >= 1) {
        result = "" + parseInt(minC) + "分钟前"
      } else {
        result = "刚刚"
      }
      return result
    },
    //回复按钮
    reback(id, name, index) {
      this.goinput(id, name);
      this.backid = id;
      this.baindex = index
    }
  },
  created() {
    this.$axios.get('adminserver/getmsg').then(res => {
      this.msglist = res.data;
    });
  },
  beforeRouteLeave(to, from, next) {
    this.$refs.heiziPowdn.btscroll.destroy();
    next()
  },
  activated(){
    mui.plusReady(function(){
      plus.navigator.setStatusBarStyle('dark');
    });

  }

}



