      let _this = this;
      let canhi = hiarr[0];
      let yhi = [0];
      for (let i = 0; i < hiarr.length; i++) {
        if (i !== 0) {
          canhi = canhi + hiarr[i];
        }
        if (i < hiarr.length - 1) {
          yhi.push(canhi);

        }

