      let canvas = document.createElement("canvas");
      canvas.width = x[0];
      let context = canvas.getContext("2d");
      const encoderOptions = 1;
      dataList.map((ele, i) => {
        let myImage = new Image();
        canvas.height = canhi;
        myImage.crossOrigin = 'Anonymous';
        myImage.src = ele; //你自己本地的图片或者在线图片
        myImage.onload = () => {
          context.drawImage(myImage, 0, yhi[i], x[i], hiarr[i]);
          document.getElementById('maximg').src = canvas.toDataURL("image/png", encoderOptions);
        };

      })
