import BScroll from 'better-scroll';
import footline from '../../components/footline'
import rp from "request-promise"
import cheerio from 'cheerio'

export default {
  components: {footline},
  props: {
    xuanid: {
      type: Number,
    },
  },

  data() {
    return {
      options: {
        // pullDownRefresh: {
        //   threshold: parseInt(document.querySelector('html').style.fontSize.replace(/px/g, '')), // 当下拉到超过顶部 50px 时，触发 pullingDown 事件
        //   stop: parseInt(document.querySelector('html').style.fontSize.replace(/px/g, '')) // 刷新数据的过程中，回弹停留在距离顶部还有 20px 的位置
        // },
        click: true,
        probeType: 3,
        mouseWheel: true,
        bounceTime: 500,//回弹时长,
      },
      xuanspi: {
        loop: true, //开启循环模式
        // autoplay: {
        //   disableOnInteraction: false,
        // },
        speed: 500,//轮播速度,
        slidesPerView: 3,
        // effect: 'cube',//首页切换立方
        effect: 'coverflow',//走马灯
        centeredSlides: true,//设定为true时，active slide会居中，而不是默认状态下的居左。
        coverflowEffect: {
          rotate:20,//slide做3d旋转时Y轴的旋转角度。默认50。
          stretch: -70,//每个slide之间的拉伸值，越大slide靠得越紧。 默认0。
          depth: 120,//slide的位置深度。值越大z轴距离越远，看起来越小。 默认100。
          modifier: 2,//depth和rotate和stretch的倍率，相当于depth*modifier、rotate*modifier、stretch*modifier，值越大这三个参数的效果越明显。默认1。
          slideShadows: false,//开启slide阴影。默认 true。
        },
        pagination: {
          el: `.xuan-doint`,
          bulletElement: 'li',
        },
      },
      swperUrl: "",
      xuanlist: []

    }
  },
  methods: {
    chuck(arr, len) {
      let arrLength = arr.length
      let result = []
      for (let i = 0; i < arrLength; i += len) {
        result.push(arr.slice(i, i + len))
      }
      return result
    },
    xuanchange() {
      setTimeout(res => {
        let id = document.querySelector('.xuan-title-box .swiper-slide-active').childNodes[0].lastChild.currentSrc;
        let back = document.querySelector('.item-back');
        back.style.backgroundImage = `url(${id})`
      }, 500)

    },
    gogo() {
      let url = 'https://recommend.samh.xndm.tech:6443/top/classify/channel?channel_id=1&version=3.2.4';//玄幻男
      let url2 = 'https://recommend.samh.xndm.tech:6443/top/classify/channel?channel_id=2';//女
      rp(url).then(body => {
        let data = JSON.parse(body).data[0].comic_info;
        data.map(item => {
          console.log(`http://image.samh.xndm.tech/cartoon/standard_cover/${item.comic_id}.jpg-300x400.jpg.webp`);
        })
      }).catch(err => {
        console.log(err);
      })
    },

    godetail(id) {
      this.$router.push(`detail${id}`)

    },
    checkhead(e) {
      let scrolly = -this.xuanscroll.y;
      let Headtitle = document.querySelector(".changeBack-box");
      let tabs = document.querySelector('.vux-tab');
        Headtitle.style.backgroundColor = `rgba(255,255,255,${scrolly / 150})`;
        if (scrolly / 220 < 0.4) {
          mui.plusReady(function () {
            plus.navigator.setStatusBarStyle('light');
          });
          tabs.className = 'vux-tab vux-tab1 scrollable';
          Headtitle.id = 'changeBack-box'
        } else {
          mui.plusReady(function () {
            plus.navigator.setStatusBarStyle('dark');
          });
          tabs.className = 'vux-tab vux-tab2 scrollable';
          Headtitle.id = 'changeBack-box2'
        }

    },
    _xuanScrol() {
      this.xuanscroll = new BScroll(document.querySelector(`.betsc-xuan-box`), this.options);
      if(document.querySelector('.betsc-xuan').style.transform){
        let scrollT=document.querySelector('.betsc-xuan').style.transform.split(',')[1].split('px)')[0]*1;
        this.xuanscroll.scrollTo(0,scrollT)
      }
      let size = parseInt(document.querySelector('html').style.fontSize.replace(/px/g, ''));
      let Headtitle = document.querySelector(".changeBack-box");
      let tabs = document.querySelector('.vux-tab');
      this.xuanscroll.on('scroll', (pos) => {
        let scrollY = Math.round(pos.y);
        let scrolly = -Math.round(pos.y);
        if (scrolly >= 0) {
          if (scrolly / 220 <= 1.1) {
            Headtitle.style.backgroundColor = `rgba(255,255,255,${scrolly / 150})`;
            if (scrolly / 220 < 0.4) {
              mui.plusReady(function () {
                plus.navigator.setStatusBarStyle('light');
              });
              tabs.className = 'vux-tab vux-tab1 scrollable';
              Headtitle.id = 'changeBack-box'
            } else {
              mui.plusReady(function () {
                plus.navigator.setStatusBarStyle('dark');
              });
              tabs.className = 'vux-tab vux-tab2 scrollable';
              Headtitle.id = 'changeBack-box2'
            }
          }
          Headtitle.style.display = 'flex';
          tabs.style.opacity = 1
          // Headtitle.style.backgroundColor = `rgba(245,72,130,${scrollY / 220})`;
        } else if (scrolly <= -2) {
          tabs.style.opacity = 1 / (-scrolly);
          Headtitle.style.display = 'none';
          if (scrolly <= -20) {
            tabs.style.opacity = 0
          }
        }

      })
    },
  },
  created() {
    let xuanswp=[]
    let data = []
    let url = `https://recommend.samh.xndm.tech:6443/top/classify/channel?channel_id=1&version=3.2.4`;
    let Url = `http://www.qiman6.com/sort/3-${this.xuanid}.html`;
    //https://recommend.samh.xndm.tech:6443/top/classify/channel?channel_id=1&version=3.2.4
    rp(url).then(body => {
      JSON.parse(body).data[0].comic_info.map(item => {
        xuanswp.push(`http://image.samh.xndm.tech/cartoon/standard_cover/${item.comic_id}.jpg-300x400.jpg.webp`)
      });
      this.swperUrl=xuanswp
    }).catch(err => {
      console.log(err);
    })
    rp(Url).then(body => {
      let $ = cheerio.load(body);
      let libox = $('.bookList_3')[0].children;
      for (let i = 1; i < libox.length - 1; i++) {
        if (libox[i].children[1]) {
          data.push({
            id: libox[i].children[1].attribs.href,
            img: libox[i].children[2].children[0].children[1].attribs.src,
            title: libox[i].children[4].children[1].attribs.title,
            last: libox[i].children[2].children[1].children[0].children[0] ? libox[i].children[2].children[1].children[0].children[0].data : '暂无',
            auto: libox[i].children[6].children[1].children[0] ? libox[i].children[6].children[1].children[0].data : '佚名'
          })

        } else {
          data.push({
            id: libox[i].children[0].children[1].attribs.href,
            img: libox[i].children[0].children[2].children[0].children[1].attribs.src,
            title: libox[i].children[0].children[4].children[1].attribs.title,
            last: libox[i].children[0].children[2].children[1].children[0].children[0] ? libox[i].children[0].children[2].children[1].children[0].children[0].data : '暂无',
            auto: libox[i].children[0].children[6].children[1].children[0] ? libox[i].children[0].children[6].children[1].children[0].data : '佚名'
          })

        }

      }

      let listdata = {};
        let silist = data.slice(0, 12);
        let folist = data.slice(12);
        listdata.six = this.chuck(silist, 6);
        listdata.four = this.chuck(folist, 4);
        for (let i = 0; i < 2; i++) {
          this.xuanlist.push({
            six: listdata.six[i],
            four: listdata.four[i]
          })
        }
        this.xuanlist[0].id = '十月精选新作来袭';
        this.xuanlist[0].id2 = '少年传奇故事';
        this.xuanlist[1].id = '十月精选新作来袭';
        this.xuanlist[1].id2 = '玄幻之奇遇';
    }).catch(err => {
      console.log(err);
    })

  },
  activated() {
    this.$nextTick(() => {
      this._xuanScrol();
    });
  }
}



