export default {
  methods: {
    goend(){
      let topbox = document.querySelector('#totop');
      let htmlh=document.querySelector('html').clientHeight;
      let time2 = setInterval(() => {
        topbox.scrollTop = topbox.scrollTop + 500;
        if (topbox.scrollHeight-topbox.scrollTop<=htmlh) {
          clearInterval(time2)
        }
      }, 0);
    },
    clicktop(e) {
      let topbox = document.querySelector('#totop');
      let time1 = setInterval(() => {
        topbox.scrollTop = topbox.scrollTop - 500;
        if (topbox.scrollTop <= 0) {
          clearInterval(time1)
        }
      }, 0);
    }
  },

}



