import BScroll from 'better-scroll'
import {Toast} from 'vant';
export default {
  props: {
    axioscode: {
      type: Number,
    },
    scrolclass: {
      type: String,
    },
  },
  data() {
    return {
      options: {
        pullDownRefresh: {
          threshold: parseInt(document.querySelector('html').style.fontSize.replace(/px/g, '')) * 0.8, // 当下拉到超过顶部 50px 时，触发 pullingDown 事件
          stop: parseInt(document.querySelector('html').style.fontSize.replace(/px/g, '')) * 0.8,// 刷新数据的过程中，回弹停留在距离顶部还有 20px 的位置
        },
        click: true,
        probeType: 3,
        mouseWheel: true,
        bounceTime: 300//回弹时长
      },
      heizitext: '正在连接漫星球...',
      BIBI: false,
      heiziimg:require('../assets/font/heizi.gif'),

    };
  },
  methods: {
    pudnrefh() {
      this.BIBI = true;
      this.$emit('puwngetdata');
    },
    _initScroll() {
      this.btscroll = new BScroll(document.querySelector(`.${this.scrolclass}`), this.options);
      if(document.querySelector(`.${this.scrolclass}`).children[0].style.transform){
        let scrollT=document.querySelector(`.${this.scrolclass}`).children[0].style.transform.split(',')[1].split('px)')[0]*1;
        this.btscroll.scrollTo(0,scrollT)
      }
      let size = parseInt(document.querySelector('html').style.fontSize.replace(/px/g, '')) * 0.8;
      // 下拉
      this.btscroll.on('pullingDown', (po) => {
        this.pudnrefh() //获取数据方法
      });
      this.btscroll.on('scroll', (pos) => {
        let scrollY = Math.round(pos.y);
        let heizi = this.$refs.heizi;
        if (scrollY >= size) {
          this.heizitext = '松开即可着陆！'
          heizi.style.backgroundImage =`url('${this.heiziimg}')`
          if (scrollY === size) {
            heizi.style.height = (0.3 * size + scrollY) + 'px'
            heizi.style.width = (0.6 * size + scrollY) + 'px'
            // heizi.style.height = (0.3 * size + scrollY) + 'px'
            // heizi.style.width = (0.6 * size + scrollY) + 'px'
          }
        } else if (scrollY < size) {
          heizi.style.backgroundImage = `url(${require('../assets/heizibudong.png')})`
          heizi.style.height = (0.3 * size + scrollY) + 'px';
          heizi.style.width = (0.6 * size + scrollY) + 'px';
          if (this.BIBI) {
            heizi.style.backgroundImage = `url('${this.heiziimg}')`
          }
        }
        if (scrollY === size && this.BIBI) {
          this.heizitext = '哔哔~连接成功!'
        } else if (scrollY < size && !this.BIBI) {
          this.heizitext = '正在连接漫星球...'
        }
      });

    },
  },
  activated() {
    this.$nextTick(() => {
      this._initScroll();
      this.btscroll.refresh()
    })
  },
  watch: {
    axioscode: {
      handler(newName, oldName) {
        if (newName === 200) {
          this.heizitext = '哔哔~连接成功!'
          Toast({duration: 1500, message: '刷新成功！'});
        } else if (newName === 0) {
          setTimeout(() => {
            this.btscroll.finishPullDown()
            this.btscroll.finishPullUp()
            this.btscroll.refresh() //重新计算元素高度
            this.heizitext = '正在连接漫星球...'
            this.BIBI = false
          })
        } else if (newName === 400) {
          setTimeout(() => {
            this.heizitext = '呲呲~连接失败啦!'
            this.btscroll.finishPullDown()
            this.btscroll.finishPullUp()
            this.btscroll.refresh() //重新计算元素高度
            this.heizitext = '正在连接漫星球...'
            this.BIBI = false
          }, 1500)

        }
      },
      immediate: true
    }
  }

};



