//     const $content = document.querySelector('#cen');
//     const scrollTop = $content ? $content.scrollTop : 0;
//     from.meta.scrollTop = scrollTop;
//   }
//   next();
// });
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



