export default {
  props: {
    procssval: {
      type: Number,
    }
  },
  data() {
    return {};
  },
  methods: {
    updown() {
      this.$emit('gogotop',2221)
    }
  },
  watch: {
    procssval: {
      handler(newName, oldName) {
        this.$nextTick(() => {
          let water = document.getElementById("water");
          water.style.transform = 'translate(0' + ',' + (100 - newName) + '%)';
        })
      },
      immediate: true
    }
  }


}



