import rp from "request-promise"
import cheerio from "cheerio"
import BScroll from 'better-scroll';
import footline from '../../components/footline'
import huojian from '../../components/btscroll/huojian'
import {Toast} from 'vant';

export default {
  components: {footline, huojian},
  props: {
    swpindex: {
      type: Number,
    }
  },
  data() {
    return {
      Footerinfo: 0,
      comiclist: [],
      commicpro: '',
      homeTop: '',
      swperUrl: '',
      spipertitle: '',
      heizitext: '正在连接漫星球...',
      BIBI: false,
      options: {
        pullDownRefresh: {
          threshold: parseInt(document.querySelector('html').style.fontSize.replace(/px/g, '')), // 当下拉到超过顶部 50px 时，触发 pullingDown 事件
          stop: parseInt(document.querySelector('html').style.fontSize.replace(/px/g, '')) // 刷新数据的过程中，回弹停留在距离顶部还有 20px 的位置
        },
        click: true,
        probeType: 3,
        mouseWheel: true,
        bounceTime: 500//回弹时长
      },
      swpoption: {
        loop: true, //开启循环模式
        autoplay: {
          disableOnInteraction: false,
        },
        speed: 500,//轮播速度,
        pagination: {
          el: '.swiper-pagination',
          bulletElement: 'li',
        },
      },
      itemimg: {
        img1: require('../../assets/fineicon/icon_classify_boy.png'),
        img2: require('../../assets/fineicon/icon_rank_boy.png'),
        img3: require('../../assets/fineicon/icon_classify_boy_pull.png'),
        img4: require('../../assets/fineicon/icon_rank_boy_pull.png'),
      },
      heiziimg: require('../../assets/font/heizi.gif'),
    }
  },
  methods: {
    checkhead() {
      let scrolly = -this.btscroll.y;
      let Headtitle = document.querySelector(".changeBack-box");
      let tabs = document.querySelector('.vux-tab');
      Headtitle.style.backgroundColor = `rgba(255,255,255,${scrolly / 150})`;
      if (scrolly / 220 < 0.4) {
        mui.plusReady(function () {
          plus.navigator.setStatusBarStyle('light');
        });
        tabs.className = 'vux-tab vux-tab1 scrollable'
        Headtitle.id = 'changeBack-box'
      } else {
        mui.plusReady(function () {
          plus.navigator.setStatusBarStyle('dark');
        });
        tabs.className = 'vux-tab vux-tab2 scrollable'
        Headtitle.id = 'changeBack-box2'
      }


    },
    gomsg() {
      this.$router.push('/message')
    },
    pulldown() {
      this.BIBI = true;
      this.heizitext = '哔哔~连接成功!';
      this.$axios.get('monman/allbook').then(res => {
        this.$store.commit('setallbook',res.data)
      }).catch(err=>{
        console.log(err);
      });
      Toast({duration: 2000, message: '刷新成功Σ(⊙▽⊙")！'});

      setTimeout(() => {
        this.btscroll.finishPullDown()
        this.btscroll.finishPullUp()
        this.btscroll.refresh() //重新计算元素高度
        this.heizitext = '正在连接漫星球...'
        this.BIBI = false
      }, 1000);
      this.swperUrl = this.swperUrl.reverse()
    },
    chuck(arr, len) {
      let arrLength = arr.length;
      let result = [];
      for (let i = 0; i < arrLength; i += len) {
        result.push(arr.slice(i, i + len))
      }
      return result
    },
    gorank() {
      this.$emit('gorank', 4)
    },
    swpipergo(title) {
    },
    getswper() {
      // let url1 = 'https://recommend.samh.xndm.tech:6443/top/homepage';//苹果女
      // let url2 = `https://recommend.samh.xndm.tech:6443/top/v3/homepage?version=3.2.4&uid=1600786060805450262`;//安卓女
      let url3 = `https://recommend.samh.xndm.tech:6443/top/v3/homepage?version=3.2.4`;//安卓男
      let data = [];
      rp(url3).then(body => {
        JSON.parse(body).data[0].operation_info.map(item => {
          data.push({"img": item.mgResourceVO.url})
        })
        this.swperUrl = data.reverse()
      }).catch(err=>{
        this.swperUrl=[{img:'https://image.samh.xndm.tech/operation/new_test/108078.webp-1200x1100.webp'},{img:"https://image.samh.xndm.tech/operation/new_prod/107886.webp-1200x1100.webp"},{img:"https://image.samh.xndm.tech/operation/new_test/107289.webp-1200x1100.webp"},{img:"https://image.samh.xndm.tech/operation/new_test/107389.webp-1200x1100.webp"}]

      })
    },
    godetail(url) {
      // let id = url.split("/")[1];
      // let URL = id[id.length - 2];
      this.$router.push(`detail${url}`)
    },

    _initScroll() {
      this.btscroll = new BScroll(document.querySelector('.finpro'), this.options);
      if (document.querySelector('.Comic-contentbox').style.transform) {
        let scrollT = document.querySelector('.Comic-contentbox').style.transform.split(',')[1].split('px)')[0] * 1;
        this.btscroll.scrollTo(0, scrollT)
      }
      let size = parseInt(document.querySelector('html').style.fontSize.replace(/px/g, ''));
      let Headtitle = document.querySelector(".changeBack-box");
      let heizi = this.$refs.heizi;
      let tabs = document.querySelector('.vux-tab');
      let hjbox=document.querySelector('#gotop1');
      this.btscroll.on('pullingDown', (po) => {
        this.pulldown() //获取数据方法
      });
      this.btscroll.on('scroll', (pos) => {
        let scrollY = Math.round(pos.y);
        let scrolly = -Math.round(pos.y);
        if(scrolly>=500){
          hjbox.style.bottom=1.5+'rem'
        }else{
          hjbox.style.bottom=-1.5+'rem'
        }
        if (scrolly >= 0) {
          if (scrolly / 220 <= 1.1) {
            Headtitle.style.backgroundColor = `rgba(255,255,255,${scrolly / 150})`;
            if (scrolly / 220 < 0.4) {
              mui.plusReady(function () {
                plus.navigator.setStatusBarStyle('light');
              });
              tabs.className = 'vux-tab vux-tab1 scrollable'
              Headtitle.id = 'changeBack-box'
            } else {
              mui.plusReady(function () {
                plus.navigator.setStatusBarStyle('dark');
              });
              tabs.className = 'vux-tab vux-tab2 scrollable'
              Headtitle.id = 'changeBack-box2'
            }
          }
          Headtitle.style.display = 'flex';
          tabs.style.opacity = 1
          // Headtitle.style.backgroundColor = `rgba(245,72,130,${scrollY / 220})`;
        } else if (scrolly <= -2) {
          tabs.style.opacity = 1 / (-scrolly);
          Headtitle.style.display = 'none';
          if (scrolly <= -20) {
            tabs.style.opacity = 0
          }
        }
        if (scrollY >= size) {
          this.heizitext = '松开即可着陆！'
          heizi.style.backgroundImage = `url('${this.heiziimg}')`
          if (scrollY === size) {
            heizi.style.height = (0.3 * size + scrollY) * 0.95 + 'px'
            heizi.style.width = (0.6 * size + scrollY) * 0.95 + 'px'
          }
        } else if (scrollY < size) {
          heizi.style.backgroundImage = `url(${require('../../assets/heizibudong.png')})`
          heizi.style.height = (0.3 * size + scrollY) * 0.95 + 'px'
          heizi.style.width = (0.6 * size + scrollY) * 0.95 + 'px'
          if (this.BIBI) {
            heizi.style.backgroundImage = `url(''${this.heiziimg}'')`
          }
        }
        if (scrollY === size && this.BIBI) {
          this.heizitext = '哔哔~连接成功!'
        } else if (scrollY < size && !this.BIBI) {
          this.heizitext = '正在连接漫星球...'
        }
      })
    },
    prolist() {
      this.$axios.get('adminserver/getfine').then(res => {
        this.comiclist = res.data;
      })
    }

  },
  activated() {
    this.$nextTick(() => {
      this._initScroll()
    });

  },
  created() {
    this.prolist()
    this.getswper()
  },


}



