import Comichead from './chuanyue'
export default {
  components:{
    Comichead
  },
  data() {
    return {
      updateID:2

    }

  },
  methods: {
    loadmore(){
      this.$axios.get('/comics/update',{id:this.updateID}).then(res=>{
        this.updateID=this.updateID+1
        console.log(res)
      })
    }

  },
  created(){
    this.$axios.get('/comics/update',{id:1}).then(res=>{
      console.log(res)
    })
  }
}



