import {Dialog, Notify} from 'vant';
import Login from './login'
import Reject from './rejister'
import Head from './head'
import BScroll from 'better-scroll'

export default {
  components: {
    Login, Reject, Head
  },
  data() {
    return {
      //隐藏显示登陆注册
      headname: '',
      setdata: false,
      options: {
        click: true,
        probeType: 3,
        mouseWheel: true,
        bounceTime: 300 //回弹时长
      },
      loginshow: false,
      rejshow: false,
      //隐藏显示登陆注册
      showset: false,
      // Footerinfo: 2,
      // activeKey: 3,
      loginInfo: false,
      username: '',
      formdata: {
        username: '',
        password: ''
      },
      formdata2: {
        username: '',
        password: '',
        password2: ''
      },
    }
  },
  methods: {
    goedit() {
      console.log(1111);
    },
    gomsg(){
      this.$router.push('/message')
    },
    goshequ(){
      window.location.href='https://jq.qq.com/?_wv=1027&k=J8uaCNUh';
    },
    gohelp() {
      Dialog.confirm({
        title: '添加任何漫画请在留言区留言完整漫画名，加入红豆社区了解更多',
        confirmButtonColor: '#fd7c51',
        confirmButtonText: '知道了',
        cancelButtonColor: '#aaa',
        cancelButtonText: '取消',
        width: '6.5rem',
        className: 'person-dialog',
      }).then(() => {
      }).catch(() => {
      });
    },
    hidset() {
      this.setdata = false
      this.$store.commit('tar', true);
    },
    //点击每一个出来list
    goset(e) {
      if(this.$refs.perhead){
        this.$refs.perhead.setinfo()
      }
      this.headname = e;
      this.setdata = true;
      this.$store.commit('tar', false);
    },
    hidrej(b) {
      this.rejshow = b
    },
    hidlogin(a) {
      this.loginshow = a
    },

    //图片上传
    onRead(e) {
      if (e.file.size <= 10000000) {
        let imgid = new FormData();
        imgid.append("file", 'multipart');
        imgid.append("Filedata", e.file);
        // imgid.append('file', e.file);
        this.$axios.post('https://api.uomg.com/api/image.360', imgid).then(res => {
          if (res.code === 1) {
            let url = res.imgurl
            this.$axios.post('admin/upavatar', {avatar: url, username: this.$store.state.username}).then(res => {
              Notify({
                message: '头像修改成功！',
                duration: 1500,
                background: '#f54882',
              });
              this.$store.commit('keeptavatar', url);
            })
          } else {
            Notify({
              message: '哦不头像上传失败了',
              duration: 1500
            });
          }
        }).catch(err => {
          Notify({
            message: '哦不头像上传失败了',
            duration: 1500
          });
        })

      } else {
        console.log('图片过大')
      }
    },

    gologin() {
      this.loginshow = true
      this.$store.commit('tar', false);
    },
    //签到
    gotest() {

    },
    redcard() {
      Dialog.confirm({
        title: '点击右上角，加入红豆社区，可领取红卡，解锁隐藏内容',
        confirmButtonColor: '#fd7c51',
        confirmButtonText: '知道了',
        cancelButtonColor: '#aaa',
        cancelButtonText: '取消',
        width: '6.5rem',
        className: 'person-dialog',
      }).then(() => {
      }).catch(() => {
      });
    },
    golove(){
      this.$router.push('ilove/1/')
      this.$store.commit('setfoot', 2);
    },
    gohis(){
      this.$router.push('ilove/2/')
      this.$store.commit('setfoot', 2);
    },
    _initScroll() {
      this.btscroll = new BScroll(document.querySelector('.user-content'), this.options);
      if(document.querySelector('.user-content-scroll').style.transform){
        let scrollT=document.querySelector('.user-content-scroll').style.transform.split(',')[1].split('px)')[0]*1;
        this.btscroll.scrollTo(0,scrollT)
      }

    },
  },
  activated() {
    mui.plusReady(function(){
      plus.navigator.setStatusBarStyle('dark');
    });
    this.$nextTick(() => {
      this._initScroll();
    });
    if(this.$store.state.logindata){
      this.$axios.post('admin/login', {username: this.$store.state.username, password: this.$store.state.password}).then(res => {
        if (res.code === 200) {
          this.$store.commit('keepuser', res.data);
        }
      }).catch(err=>{
        console.log(err);
      })
    }

  },
  beforeRouteLeave(to, from, next) {
    this.btscroll.destroy();
    next()
  },

}



