import Gback from '../../components/Gback'
import loading from '../../components/loading'
import procss from '../../components/waterup'
import totop from '../../components/totop'
import login from '../person/login'
import rej from '../person/rejister'
import rp from "request-promise"
import cheerio from "cheerio"
import {Toast} from 'vant'
import qs from 'qs'
import {Dialog} from 'vant';

export default {
  components: {Gback, loading, totop, procss, login, rej},
  data() {
    return {
      starlight: require('../../assets/ic_detailtab_light.png'),
      stardark: require('../../assets/ic_detailtab_dark.png'),
      //续看第几话
      his: 1,
      //登陆注册显示
      loginshow: false,
      rejshow: false,
      //登陆注册显示
      tabsactive: 0,
      //是否已经收藏了
      iflove: false,
      //下拉加载的控制
      Dodnloadingnext: false,
      Dodnloading: false,
      comicdetail: '',
      comiclist: '',
      reverseinfo: true,
      loadshow: false,
      comicpic: '',
      current: 0,
      showtop: false,
      showPic: false,
      showgonext: false,
      mu: '',
      title: '',
      //区分上面的pic这个是具体的一章的所有图画的数组
      comicpicitemlist: '',
      //点击获取到的本章节的地址
      picitemhref: '',
      spinier: false,
      procssshow: false,
      finished: false,
      procssval: 0,
      //havenone是最后一话加载完状态
      havenone: false,
      //是否可以加载下一话
      cannext: false,
    }
  },
  methods: {
    ShowTop(e) {
      this.showtop = !this.showtop;
    },
    gogotop(e) {
      this.showtop = !this.showtop;
    },
    //在本页面登陆完之后修改收藏信息
    loginstar() {
      let ilove = this.$store.state.ilove;
      let parid = this.$route.params.id;
      for (let i = 0; i < ilove.length; i++) {
        if (ilove[i].id === parid) {
          this.his = ilove[i].his;
          this.iflove = true;
          break;
        }
      }

    },
    //收藏
    getlove() {
      //如果登录信息为空则弹出登录框
      if (!this.$store.state.logindata) {
        this.loginshow = true
      } else {
        //如果登录了
        let ilovearr = this.$store.state.ilove;
        let comicdetail = this.comicdetail;
        let lovebox = document.querySelector('.ilove-box-ilove-content-img');
        let text = document.querySelector('.ilove-span');
        if (this.iflove) {
          //点击取消收藏
          let parid = this.$route.params.id;
          for (let i = 0; i < ilovearr.length; i++) {
            if (ilovearr[i].id === parid) {
              ilovearr.splice(i, 1)
              break;
            }
          }
          this.$axios.post('admin/ilove',
            {
              username: this.$store.state.username,
              password: this.$store.state.password,
              ilove: ilovearr
            }).then(res => {
            if (res.code === 200) {
              this.$store.commit('set_ilove', ilovearr);
              Toast({message: '收藏已经取消了(´Д｀ ;)', duration: 1500})
              lovebox.style.backgroundImage = `url('${this.stardark}')`
              this.iflove = false;
              text.innerText = '收藏'
              text.style.color = '#000'
            }
          })
        } else {
          //添加收藏
          ilovearr.unshift({
            img: comicdetail.img,
            his: this.mu === '' ? 1 : this.mu,
            last: comicdetail.zz,
            title: comicdetail.title,
            id: comicdetail.id,
            fromid:comicdetail.fromid
          });
          this.$axios.post('admin/ilove',
            {
              username: this.$store.state.username,
              password: this.$store.state.password,
              ilove: ilovearr
            }).then(res => {
            if (res.code === 200) {
              this.$store.commit('set_ilove', ilovearr);
              Toast({message: '收藏作品成功~', duration: 1500})
              lovebox.style.backgroundImage = `url('${this.starlight}')`
              text.innerText = '已收藏'
              text.style.color = '#dcdcdc'
              this.iflove = true;
            }
          })

        }

      }

    },
    //继续阅读
    goon() {
      this.gocomicpic(this.comiclist[this.his - 1].id, this.comiclist[this.his - 1].name, this.his - 1);
    },
    hidlogin(a) {
      this.loginshow = a
    },
    hidrej(b) {
      this.rejshow = b
    },
    swipeTo(e) {
      this.$refs.swp.swipeTo(e)
    },
    swperchange(index) {
      this.tabsactive = index
      let vanttab = document.querySelector('.detail-project');
      if (index === 1) {
        vanttab.style.height = 11 + 'rem';
        vanttab.style.overflowY = 'scroll';
        let detbox = document.querySelector('.comictdetail');
        let size = Math.ceil(document.querySelector('html').style.fontSize.replace(/px/g, '') * 5);
        if (detbox.scrollTop >= size) {
          detbox.scrollTop = size;
        }
      } else {
        vanttab.style.height = '100%';
      }
    },
    fixvant(index, title) {
      this.swipeTo(index)
    },
    errsearch() {
      this.$router.push('/search')
    },
    back() {
      this.$router.back(-1)

    },
    //下一章节
    loadnext() {
      setTimeout(() => {
        let mu = this.mu;
        if (mu === this.comiclist.length) {
          this.havenone = true
        } else {
          this.Dodnloading = true;
          if (this.havenone) {
            this.havenone = false
          }
          setTimeout(() => {
            if (this.$route.params.id.charCodeAt(0) < 57) {
              rp(`http://m.qiman6.com/${this.$route.params.id}/${this.comiclist[mu].id}.html`).then(body => {
                let $ = cheerio.load(body);
                let str233 = $('body script')[0].children[0].data.split('return p}(')[1].slice(0, -3).split(',');
                if (this.iflove) {
                  let ilovearr = this.$store.state.ilove;
                  for (let i = 0; i < ilovearr.length; i++) {
                    if (ilovearr[i].id === this.$route.params.id) {
                      ilovearr[i].his = this.mu;
                      break;
                    }
                  }
                  this.$axios.post('admin/ilove',
                    {
                      username: this.$store.state.username,
                      password: this.$store.state.password,
                      ilove: ilovearr
                    }).then(res => {
                    if (res.code === 200) {
                      this.$store.commit('set_ilove', ilovearr);
                      this.his = this.mu
                    }
                  })
                }
                let allitem = document.querySelectorAll('.detail-list-box');
                allitem.forEach((item) => [
                  item.style.color = '#000'
                ])
                allitem[mu].style.color = '#1DA1F2';
                this.showgonext = false;
                this.title = this.comiclist[mu].name;
                this.mu = this.mu + 1;
                this.detailscro(str233).map(item => {
                  this.comicpic.push(item)
                  setTimeout(() => {
                    this.Dodnloading = false;
                    this.cannext = true;
                  }, 1000)
                });
              }).catch(err => {
                console.log(err);
              })
            }else {
              this.$axios.get('adminserver/comicimg', {id: this.comiclist[mu].id}).then(res => {
                if (res.code === 200) {
                  if (this.iflove) {
                    let ilovearr = this.$store.state.ilove;
                    for (let i = 0; i < ilovearr.length; i++) {
                      if (ilovearr[i].id === this.$route.params.id) {
                        ilovearr[i].his = this.mu;
                        break;
                      }
                    }
                    this.$axios.post('admin/ilove',
                      {
                        username: this.$store.state.username,
                        password: this.$store.state.password,
                        ilove: ilovearr
                      }).then(res => {
                      if (res.code === 200) {
                        this.$store.commit('set_ilove', ilovearr);
                        this.his = this.mu
                      }
                    })
                  }
                  let allitem = document.querySelectorAll('.detail-list-box');
                  allitem.forEach((item) => [
                    item.style.color = '#000'
                  ])
                  allitem[mu].style.color = '#1DA1F2';
                  this.showgonext = false;
                  this.title = this.comiclist[mu].name;
                  this.mu = this.mu + 1;
                  res.data.map(item => {
                    this.comicpic.push(item)
                    setTimeout(() => {
                      this.Dodnloading = false;
                      this.cannext = true;
                    }, 1000)
                  });
                }
              }).catch(err => {
                console.log(err);
              })
            }


          },)
        }
      }, 0)
    },
    gocomicpic(href, title, index) {
      setTimeout(() => {
        this.procssval = 0;
        this.Dodnloadingnext = true;
        if (this.havenone) {
          this.havenone = false
        }
        if (index + 1 === this.comiclist.length) {
          this.havenone = true
        }
        this.int1 = setInterval(() => {
          this.procssval = this.procssval + 1
          if (this.procssval === 40) {
            clearInterval(this.int1);
            this.int2 = setInterval(() => {
              this.procssval = this.procssval + 1
              if (this.procssval === 90) {
                clearInterval(this.int2);
              }
            }, 100)
          }
        }, 40)
        let allitem = document.querySelectorAll('.detail-list-box')
        setTimeout(() => {
          allitem.forEach((item) => [
            item.style.color = '#000'
          ])
          allitem[index].style.color = '#1DA1F2'
        }, 500)
        this.procssshow = true;
        this.comicpic = ''
        this.showPic = true
        this.title = title;
        this.mu = index + 1
        if (this.$route.params.id.charCodeAt(0) < 57) {
          rp(`http://m.qiman6.com/${this.$route.params.id}/${href}.html`).then(body => {
            let $ = cheerio.load(body);
            let str233 = $('body script')[0].children[0].data.split('return p}(')[1].slice(0, -3).split(',')
            if (this.iflove) {
              let ilovearr = this.$store.state.ilove;
              let parid = this.$route.params.id;
              for (let i = 0; i < ilovearr.length; i++) {
                if (ilovearr[i].id === parid) {
                  ilovearr[i].his = this.mu;
                  break;
                }
              }
              this.$axios.post('admin/ilove',
                {
                  username: this.$store.state.username,
                  password: this.$store.state.password,
                  ilove: ilovearr
                }).then(res => {
                if (res.code === 200) {
                  this.$store.commit('set_ilove', ilovearr);
                  this.his = this.mu
                }
              })
            }
            this.procssval = 100;
            this.comicpic = this.detailscro(str233)
            clearInterval(this.int1);
            clearInterval(this.int2);
            setTimeout(() => {
              this.Dodnloadingnext = false;
              setTimeout(() => {
                this.procssshow = false;
              }, 200)
            }, 200)
            //2秒后才可以自动加载下一话的开关设置为true
            setTimeout(res => {
              this.cannext = true
            }, 1000)
          }).catch(err => {
            console.log(err);
            this.procssval = 100;
            clearInterval(this.int1);
            clearInterval(this.int2);
            setTimeout(() => {
              this.Dodnloadingnext = false;
              this.showPic = false
              setTimeout(() => {
                this.procssshow = false;
              }, 200)
            }, 200)
            Toast({message: '没有此章节漫画(´Д｀ ;)', duration: 2000})
          })

        } else {
          this.$axios.get('adminserver/comicimg', {id: href}).then(res => {
            if (res.code === 200) {
              if (this.iflove) {
                let ilovearr = this.$store.state.ilove;
                let parid = this.$route.params.id;
                for (let i = 0; i < ilovearr.length; i++) {
                  if (ilovearr[i].id === parid) {
                    ilovearr[i].his = this.mu;
                    break;
                  }
                }
                this.$axios.post('admin/ilove',
                  {
                    username: this.$store.state.username,
                    password: this.$store.state.password,
                    ilove: ilovearr
                  }).then(res => {
                  if (res.code === 200) {
                    this.$store.commit('set_ilove', ilovearr);
                    this.his = this.mu
                  }
                })
              }
              this.procssval = 100;
              this.comicpic = res.data
              clearInterval(this.int1);
              clearInterval(this.int2);
              setTimeout(() => {
                this.Dodnloadingnext = false;
                setTimeout(() => {
                  this.procssshow = false;
                }, 200)
              }, 200);
              //2秒后才可以自动加载下一话的开关设置为true
              setTimeout(res => {
                this.cannext = true
              }, 1000)
            }else {
              this.procssval = 100;
              clearInterval(this.int1);
              clearInterval(this.int2);
              setTimeout(() => {
                this.Dodnloadingnext = false;
                this.showPic = false
                setTimeout(() => {
                  this.procssshow = false;
                }, 200)
              }, 200)
              Toast({message: '没有此章节漫画(´Д｀ ;)', duration: 2000})
            }
          }).catch(err => {
            console.log(err);
            this.procssval = 100;
            clearInterval(this.int1);
            clearInterval(this.int2);
            setTimeout(() => {
              this.Dodnloadingnext = false;
              this.showPic = false
              setTimeout(() => {
                this.procssshow = false;
              }, 200)
            }, 200)
            Toast({message: '没有此章节漫画(´Д｀ ;)', duration: 2000})
          })


        }

      },)

      //

    },
    //点击返回列表
    backlist() {
      this.showPic = !this.showPic;
      this.showtop = false;
      this.cannext = false;
      if (this.his > 3) {
        let detbox = document.querySelector('.comictdetail');
        let size = parseInt(document.querySelector('html').style.fontSize.replace(/px/g, ''));
        let scroT = (1.88 + (this.his - 2) * 1.53) * size;
        detbox.scrollTo({
          top: scroT,
        })

      }

    },
    //正选反选
    listresverz(e) {
      if (!this.reverseinfo) {
        e.currentTarget.style.color = 'royalblue'
        e.currentTarget.nextElementSibling.style.color = '#000'
        this.comiclist.reverse()
        this.reverseinfo = true;
      }

    },
    listresverd(e) {
      if (this.reverseinfo) {
        e.currentTarget.style.color = 'royalblue'
        e.currentTarget.previousElementSibling.style.color = '#000'
        this.comiclist.reverse()
        this.reverseinfo = false;
      }
    },
    getdetail() {
      let id = this.$route.params.id;
      let Url = `http://m.qiman6.com/${id}/`;
      if (id.charCodeAt(0) < 57) {
        rp(Url).then(body => {
          let data = {}
          let $ = cheerio.load(body);
          let items = []
          data.img = $(".box-back1 img")[0].attribs.src;
          data.title = $(".box-back2 h1")[0].children[0].data;
          data.sl = '连载中';
          data.id = id;
          let debox = $(".box-back2 .txtItme");
          if (debox.length === 5) {
            data.auto = debox[1].children[0].data.split('：')[1]
            data.pro = debox[2].children[0].data.split('：')[1]
            data.time = debox[3].children[0].data.split('：')[1]
            data.last = debox[4].children[0].children[0].data.split('：')[1]
          } else {
            data.auto = debox[0].children[0].data.split('：')[1]
            data.pro = debox[1].children[0].data.split('：')[1]
            data.time = debox[2].children[0].data.split('：')[1]
            data.last = debox[3].children[0].children[0].data.split('：')[1]
          }
          let descbox = $('.detail-intro .comic-intro')[0];
          data.desc = descbox.children.length ? descbox.children[0].data.replace(/\s/g, "") : '暂无简介';
          let itembox = $('.catalog-list ul li');
          itembox.map((index, it) => {
            items.push({
              id: it.attribs['data-id'],
              name: it.children[0].children[0].data
            })
          });
          if (itembox.length === 16) {
            let body = {
              id,
              id2: 1
            };
            let opts = {
              url: 'http://m.qiman6.com/bookchapter/',
              method: "POST",
              headers: {'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'},
              body: qs.stringify(body)
            };
            rp(opts).then(bo => {
                let axbo = JSON.parse(bo);
                data.items = items.concat(axbo).reverse();
                this.comicdetail = data;
                this.comiclist = data.items;
                //添加到记录里
                let hisarr = this.$store.state.his;
                for (let i = 0; i < hisarr.length; i++) {
                  if (hisarr[i].title === data.title) {
                    hisarr.splice(i, 1);
                    break;
                  }
                }
                this.$nextTick(() => {
                  let his = {};
                  his.title = data.title;
                  his.last = data.zz;
                  his.id = data.id;
                  his.img = data.img;
                  his.time = new Date().getTime();
                  hisarr.unshift(his);
                  this.$store.commit('set_his', hisarr)
                });
                this.comicdetail.zz = this.comiclist.length;
                setTimeout(()=>{
                  this.loadshow = false;
                },500)
                this.$nextTick(res => {
                  if (this.iflove) {
                    let span = document.querySelectorAll('.detail-project-list-desc')
                    span[this.his - 1].style.color = '#1DA1F2'
                  }
                })
                return null;
              }
            ).catch(err => {
              this.loadshow = false;
              console.log(err);
              Dialog.confirm({
                title: '请求出错了呢,重新去挑选漫画吧',
                confirmButtonColor: '#fd7c51',
                confirmButtonText: '重新挑选',
                cancelButtonColor: '#000',
                cancelButtonText: '返回首页',
                width: '6.5rem',
                className: 'ilove-dialog',
              }).then(() => {
                this.$router.push('/search')

              }).catch(() => {
                this.$router.push('/')
              });
            })
          } else {
            data.items = items.reverse();
            this.comicdetail = data;
            this.comiclist = data.items;
            //添加到记录里
            let hisarr = this.$store.state.his;
            for (let i = 0; i < hisarr.length; i++) {
              if (hisarr[i].title === data.title) {
                hisarr.splice(i, 1);
                break;
              }
            }
            this.$nextTick(() => {
              let his = {};
              his.title = data.title;
              his.last = data.zz;
              his.id = data.id;
              his.img = data.img;
              his.time = new Date().getTime();
              hisarr.unshift(his);
              this.$store.commit('set_his', hisarr)
            });
            this.comicdetail.zz = this.comiclist.length;
            setTimeout(()=>{
              this.loadshow = false;
            },500)

            this.$nextTick(res => {
              if (this.iflove) {
                let span = document.querySelectorAll('.detail-project-list-desc')
                span[this.his - 1].style.color = '#1DA1F2'
              }
            })

          }
          return null;

        }).catch(err => {
          this.loadshow = false;
          console.log(err);
          Dialog.confirm({
            title: '请求出错了呢,重新去挑选漫画吧',
            confirmButtonColor: '#fd7c51',
            confirmButtonText: '重新挑选',
            cancelButtonColor: '#000',
            cancelButtonText: '返回首页',
            width: '6.5rem',
            className: 'ilove-dialog',
          }).then(() => {
            this.$router.push('/search')

          }).catch(() => {
            this.$router.push('/')
          });
        })
      } else {
        for (let i = 0; i < this.$store.state.allbook.length; i++) {
          let item = this.$store.state.allbook[i]
          if (item.id === id) {
            this.comicdetail = item;
            this.comiclist = item.items;
            //添加到记录里
            let hisarr = this.$store.state.his;
            for (let i = 0; i < hisarr.length; i++) {
              if (hisarr[i].title === item.title) {
                hisarr.splice(i, 1);
                break;
              }
            }
            this.$nextTick(() => {
              let his = {};
              his.title = item.title;
              his.last = item.zz;
              his.id = item.id;
              his.img = item.img;
              his.time = new Date().getTime();
              hisarr.unshift(his);
              this.$store.commit('set_his', hisarr)
            });
            this.comicdetail.zz = this.comiclist.length;
            this.$nextTick(res => {
              if (this.iflove) {
                let span = document.querySelectorAll('.detail-project-list-desc')
                span[this.his - 1].style.color = '#1DA1F2'
              }
            })
            setTimeout(() => {
              this.loadshow = false;
            }, 500);
            break;
          }

        }
        setTimeout(() => {
          if (!this.comiclist.length) {
            this.loadshow = false;
            Dialog.confirm({
              title: '请求出错了呢,重新去挑选漫画吧',
              confirmButtonColor: '#fd7c51',
              confirmButtonText: '重新挑选',
              cancelButtonColor: '#000',
              cancelButtonText: '返回首页',
              width: '6.5rem',
              className: 'ilove-dialog',
            }).then(() => {
              this.$router.push('/search')

            }).catch(() => {
              this.$router.push('/')
            });
          }
        }, 500)
      }

    },
    //滚轮事件，顶部的模糊图的出现与消失，背景图的模糊度
    topScroll() {
      if (!this.showPic) {
        let Headimg = document.querySelector(".detailhead");
        if (Headimg) {
          let detbox = document.querySelector('.comictdetail');
          let scroll = detbox.scrollTop;
          let Headtitle = document.querySelector("#filtTitleNone");
          let love = document.querySelector(".ilove-box");
          let font = document.querySelector("html").style.fontSize.replace(/px/g, '') * 5;
          if (scroll >= font) {
            Headtitle.style.display = 'block';
          } else {
            love.style.opacity = `${(230 - scroll) / 230}`;
            Headtitle.style.display = 'none';
          }

        }
      } else {
        this.pudownload()
      }


    },
    //划分界限下面是漫画图片的区域了
    pudownload() {
      let picbox = document.querySelector('.comicpic-itembox');
      let scroll1 = picbox.scrollHeight;
      let scroll2 = picbox.clientHeight;
      let scroll3 = picbox.scrollTop;
      let Top = scroll1 - scroll2 - scroll3;
      if (Top <= 2000 && this.cannext) {
        this.cannext = false
        this.$nextTick(() => {
          this.loadnext()
        })
      }
    },
    //暂时设置隐藏显示漫画自动加载loding
    setlodin() {
      let spiner = document.querySelector('.spinner');
      if (this.spinier) {
        spiner.style.zIndex = 9999
      } else {
        spiner.style.zIndex = -1
      }
      this.spinier = !this.spinier
    },
    func(p, a, c, k, e, d) {
      e = function (c) {
        return (c < a ? '' : e(parseInt(c / a))) + ((c = c % a) > 35 ? String.fromCharCode(c + 29) : c.toString(36))
      };
      if (!''.replace(/^/, String)) {
        while (c--) {
          d[e(c)] = k[c] || e(c)
        }
        k = [function (e) {
          return d[e]
        }];
        e = function () {
          return '\\w+'
        };
        c = 1
      }
      while (c--) {
        if (k[c]) {
          p = p.replace(new RegExp('\\b' + e(c) + '\\b', 'g'), k[c])
        }
      }
      return p
    },
    detailscro(str233) {
      let len = str233.length - 1;
      let d1 = JSON.parse(str233[len]);
      let e1 = str233[len - 1];
      let k1 = eval(str233[len - 2]);
      let c1 = str233[len - 3];
      let a1 = str233[len - 4];
      let p1 = str233.slice(0, len - 4).join().replace(/'/g, "");
      let strimg = this.func(p1, a1, c1, k1, e1, d1);
      let imgarr = JSON.parse(strimg.split('newImgs=')[1]);
      let data = [];
      imgarr.map(item => {
        data.push(item.replace(/'/g, ""))
      })
      return imgarr
    }
  },
  mounted: function () {
    // 监听（绑定）滚轮滚动事件
    this.loadshow = true;
    window.addEventListener('scroll', this.topScroll, true);
  },
  destroyed() {
    window.removeEventListener('scroll', this.topScroll)
  },
  created() {
    mui.plusReady(function () {
      plus.navigator.setStatusBarStyle('light');
    });
    this.$nextTick(() => {
      this.getdetail();
      if (this.$store.state.logindata) {
        let ilove = this.$store.state.ilove;
        let parid = this.$route.params.id;
        for (let i = 0; i < ilove.length; i++) {
          if (ilove[i].id === parid) {
            this.his = ilove[i].his;
            this.iflove = true;
            break;
          }
        }
      }
    });


  }
}



