import fine from './indexpro/fine'
import update from './indexpro/update'
import category from './indexpro/category'
import rank from './indexpro/rank'
import japan from './indexpro/japan'
import xuan from './indexpro/xuanhuan'
import lianai from './indexpro/lianai'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import rp from "request-promise"
import {Dialog, Toast} from 'vant';

export default {
components: {fine, update, category, rank, japan, xuan, Tab, TabItem, lianai},
data() {
return {
swpindex: 0,
swiperOption: {
loop: false, //开启循环模式
speed: 500,//轮播速度,

cubeEffect: {
slideShadows: false,
shadow: false,
shadowOffset: 0,
shadowScale: 0
},
},
}
},
methods: {
tosearch() {
this.$router.push('/search')
},
swperchange(e) {
if (e) {
this.swpindex = e
} else {
this.swpindex = this.$refs.bigswip.swiper.activeIndex;
}
if (this.swpindex === 0) {
this.$refs.finescroll.checkhead(this.swpindex)
} else if (this.swpindex === 1) {
this.$refs.xuan.checkhead(this.swpindex)
} else {
mui.plusReady(function () {
plus.navigator.setStatusBarStyle('dark');
});
let Headtitle = document.querySelector(".changeBack-box");
let tabs = document.querySelector('.vux-tab');
Headtitle.style.backgroundColor = `rgba(255,255,255,1)`;
tabs.className = 'vux-tab vux-tab2 scrollable';
Headtitle.id = 'changeBack-box2'
}

},
govip() {
if (this.$store.state.logindata) {
let limit = this.$store.state.user.limit
if (limit >= 1) {
Dialog.confirm({
title: `您是尊贵的红豆VIP${limit}级会员,享有多项特权`,
confirmButtonColor: '#fd7c51',
confirmButtonText: '知道了',
cancelButtonColor: '#aaa',
cancelButtonText: '取消',
width: '6.5rem',
className: 'person-dialog',
}).then(() => {
}).catch(() => {
});
} else {
Dialog.confirm({
title: '您还不是红豆会员，无法享受多种特权',
confirmButtonColor: '#fd7c51',
confirmButtonText: '知道了',
cancelButtonColor: '#aaa',
cancelButtonText: '取消',
width: '6.5rem',
className: 'ilove-dialog',
}).then(() => {
}).catch(() => {
});
}

} else {
Dialog.confirm({
title: '你还没有登录，不是红豆VIP',
confirmButtonColor: '#fd7c51',
confirmButtonText: '知道了',
cancelButtonColor: '#aaa',
cancelButtonText: '取消',
width: '6.5rem',
className: 'ilove-dialog',
}).then(() => {
}).catch(() => {
});
}
}
},
watch: {
swpindex: {
handler(newName, oldName) {
setTimeout(res => {
let line2 = document.querySelector('.vux-tab-ink-bar');
let swiper1 = this.$refs.bigswip.swiper;
if (newName === 0) {
line2.style.left = `4%`
line2.style.right = `89%`
swiper1.slideTo(0, 300, false);
} else if (newName === 1) {
line2.style.left = `18%`
line2.style.right = `75%`
swiper1.slideTo(1, 300, false);
} else if (newName === 2) {
line2.style.left = `32%`
line2.style.right = `61%`
swiper1.slideTo(2, 300, false);
} else if (newName === 3) {
line2.style.left = `46%`
line2.style.right = `47%`
swiper1.slideTo(3, 300, false);
} else if (newName === 4) {
line2.style.left = `60%`
line2.style.right = `33%`

swiper1.slideTo(4, 300, false);
} else if (newName === 5) {
line2.style.left = `74%`
line2.style.right = `19%`
swiper1.slideTo(5, 300, false);
} else if (newName === 6) {
line2.style.left = `88%`
line2.style.right = `5%`
swiper1.slideTo(6, 300, false);
}
})
},
immediate: false
}
},
mounted() {
setTimeout(res => {
let line = document.querySelectorAll('.vux-tab-ink-bar')[0]
line.style.left = `4%`
line.style.right = `89%`
}, 500)






mui.plusReady(() => {
var backcount = 0;
this.$mui.back = () => {
if (this.$mui.os.ios) return;
if (backcount > 0) {
if (window.plus) plus.runtime.quit();
return;
}
Toast({message: '再点击一次退出应用！', duration: 1500})
backcount++;
setTimeout(() => {
backcount = 0;
}, 2000);
};
})
},
created() {
rp('https://recommend.samh.xndm.tech:6443/top/classify/channel?channel_id=1&version=3.2.4').then(body => {
this.$store.commit('lianai', JSON.parse(body).data)
})
this.$axios.get('monman/allbook').then(res => {
this.$store.commit('setallbook', res.data)
}).catch(err => {
console.log(err);
})
this.$axios.get('adminserver/getpoint').then(res => {
if (res.point === this.$store.state.point) {

} else {
Dialog.confirm({
title: '您的红豆版本太旧了已经不可以使用了，请下载最新版本',
confirmButtonColor: '#fd7c51',
confirmButtonText: '知道了',
cancelButtonColor: '#aaa',
cancelButtonText: '取消',
width: '6.5rem',
className: 'ilove-dialog none-dialog',
}).then(() => {
setTimeout(() => {
let box = document.querySelector('.none-dialog')
let box2 = document.querySelector('.van-overlay')
box.style.display = 'block'
box2.style.display = 'block'
}, 500)
}).catch(() => {
setTimeout(() => {
let box = document.querySelector('.none-dialog')
let box2 = document.querySelector('.van-overlay')
box.style.display = 'block'
box2.style.display = 'block'
}, 500)
});
}

}).catch(err => {
console.log(err);
})
},
beforeRouteLeave(to, from, next) {
this.$refs.finescroll.btscroll.destroy();
this.$refs.xuan.xuanscroll.destroy();
next()
},
activated() {
mui.plusReady(function () {
plus.navigator.setStatusBarStyle('light');
});

}


}



