import {TimelineLite, Power4, Expo} from 'gsap'
export default {
  methods: {
    gotop() {
      let huojian = new TimelineLite();
      huojian
        .to("#gotop1", 1, {y: 100, opacity: 1,}, 0)
        .to("#gotop1", 1, {y: -1200, opacity: 1,}, 0.6)
        .to("#gotop1", 1, {y: 0,  opacity: 1, scale: 1,  clearProps: "all"}, "1.4");
      setTimeout(res=>{
        this.$parent.btscroll.scrollTo(0, 0, 0);
      },700)
    },
  },

}



