import rp from "request-promise"
import cheerio from "cheerio"
import footline from '../../components/footline'

export default {
  components: {footline},

  props:{
    id:{
      type:Number
    }
  },
  data() {
    return {
      list: []
    }
  },

  methods: {
    gg() {
      let arr = this.$store.state.lianai[this.id].comic_info;
      arr.map(item => {
        this.list.push({
          img: `http://image.samh.xndm.tech/cartoon/standard_cover/${item.comic_id}_2_1.jpg-800x400.jpg.webp`,
          auto: item.author_name,
          desc: item.comic_feature,
          title: item.comic_name,
          score: Math.floor(item.comic_score) / 10,
          last: item.lastchapter_title.split(' ')[0],
          shoucang: Math.floor(item.shoucang/ 10000)/10 ,
          type: item.sort_typelist,
          cont: item.count_num,
          rank: item.comic_rank,
          lastnum: item.lastchapter_urlid

        })
      });
      // this.$axios.post('adminserver/setpoint',{id:3,point:107}).then(res=>{
      //   console.log(res);
      // })
      // this.$axios.get('adminserver/getpoint').then(res => {
      //   console.log(res);
      // })
    }

  },
  created() {
    setTimeout(()=>{
      let arr = this.$store.state.lianai[this.id].comic_info;
      arr.map(item => {
        this.list.push({
          img: `http://image.samh.xndm.tech/cartoon/standard_cover/${item.comic_id}_2_1.jpg-800x400.jpg.webp`,
          auto: item.author_name,
          desc: item.comic_feature,
          title: item.comic_name,
          score: Math.floor(item.comic_score) / 10,
          last: item.lastchapter_title.split(' ')[0],
          shoucang: Math.floor(item.shoucang/ 10000)/10 ,
          type: item.sort_typelist,
          cont: item.count_num,
          rank: item.comic_rank,
          lastnum: item.lastchapter_urlid

        })
      });
    },3000)

  }
}



