import {Notify} from 'vant';

export default {
  components: {},
  data() {
    return {
      formdata: {
        username: this.$store.state.username,
        password: this.$store.state.password
      },
      formdata2: {
        username: '',
        password: '',
        password2: ''
      },
      loginInfo: '',
      agree: true,

    }
  },
  methods: {
    goback() {
      this.$emit('hidlogin', false)
      this.$store.commit('tar', true);

    },
    login() {
      if (!this.formdata.username) {
        Notify({
          message: '请输入账号!',
          duration: 1500
        });
      } else if (!this.formdata.password) {
        Notify({
          message: '请输入密码!',
          duration: 1500
        });
      } else {
        this.$axios.post('admin/login',
          {username: this.formdata.username, password: this.formdata.password}).then(res => {
          if (res.code === 200) {
            this.$store.commit('tar', true);
            this.$store.commit('CHANGE_LOGININFO', true);//是否登录
            this.$emit('hidlogin', false);
            this.$store.commit('KEEP_USERNAME', res.data.username);
            this.$store.commit('KEEP_PASSWORD', res.data.password);
            this.$store.commit('keeptavatar', res.data.avatar);
            this.$store.commit('keepuser', res.data);
            this.$store.commit('set_ilove', res.data.ilove);
            this.$emit('loginstar');
            this.$nextTick(() => {
              this.$axios.post('admin/logindress', {username: this.formdata.username}).then(res => {
                if (res.code === 200) {
                  this.$store.commit('keepuser', res.data);
                }
              })
            })
            Notify({
              message: '登录成功！',
              duration: 1500,
              background: '#409EFF',
            });
          } else {
            Notify({
              message: '账号或密码错误!',
            });
          }

        })
      }

    },
    gorej() {
      this.$emit('hidrej', true);
      this.$emit('hidlogin', false);
      this.$store.commit('tar', false);
    },
  },
  created() {
    // this.$nextTick(()=>{
    //   if(this.$store.state.logindata){
    //     this.formdata.username=this.$store.state.user.username;
    //     this.formdata.password=this.$store.state.user.password;
    //   }
    // })

  },
  mounted() {
  },
}



