  methods: {
    godetail(id) {
      this.$router.push(`/detail${id}`)

    },
    podn() {
      this.dolading = false;
      this.spilodin = true;
      let arrbox = this.catarr[this.catindex];
      let num = Math.ceil(arrbox.item.length / 20) + 1;
      if (arrbox.item.length <= 80) {
        rp(`http://www.qiman6.com/${arrbox.href}-${num}.html`).then(body => {
          let data=[]
          let $ = cheerio.load(body);
          let libox = $('.bookList_3')[0].children;
          for (let i = 1; i < libox.length - 1; i++) {
            if (libox[i].children[1]) {
              data.push({
                id: libox[i].children[1].attribs.href,
                img: libox[i].children[2].children[0].children[1].attribs.src,
                title: libox[i].children[4].children[1].attribs.title,
                last: libox[i].children[2].children[1].children[0].children[0] ? libox[i].children[2].children[1].children[0].children[0].data : '暂无',
                auto: libox[i].children[6].children[1].children[0] ? libox[i].children[6].children[1].children[0].data : '佚名'
              })

            } else {
              data.push({
                id: libox[i].children[0].children[1].attribs.href,
                img: libox[i].children[0].children[2].children[0].children[1].attribs.src,
                title: libox[i].children[0].children[4].children[1].attribs.title,
                last: libox[i].children[0].children[2].children[1].children[0].children[0] ? libox[i].children[0].children[2].children[1].children[0].children[0].data : '暂无',
                auto: libox[i].children[0].children[6].children[1].children[0] ? libox[i].children[0].children[6].children[1].children[0].data : '佚名'
              })

            }
          }
          arrbox.item = arrbox.item.concat(data)
          this.dolading = true;
          this.spilodin = false
        }).catch(err => {
          this.dolading = true;
          this.spilodin = false;
        })

      } else {
        this.dolading = false;
        this.spilodin = true;
        setTimeout(a => {
          this.haveno = true
          this.spilodin = false;
        }, 500);
      }
    },
    puwngetdata() {
      rp(`http://www.qiman6.com/${this.catarr[this.catindex].href}-1.html`).then(body => {
        let data=[]
        let $ = cheerio.load(body);
        let libox = $('.bookList_3')[0].children;
        for (let i = 1; i < libox.length - 1; i++) {
          if (libox[i].children[1]) {
            data.push({
              id: libox[i].children[1].attribs.href,
              img: libox[i].children[2].children[0].children[1].attribs.src,
              title: libox[i].children[4].children[1].attribs.title,
              last: libox[i].children[2].children[1].children[0].children[0] ? libox[i].children[2].children[1].children[0].children[0].data : '暂无',
              auto: libox[i].children[6].children[1].children[0] ? libox[i].children[6].children[1].children[0].data : '佚名'
            })

          } else {
            data.push({
              id: libox[i].children[0].children[1].attribs.href,
              img: libox[i].children[0].children[2].children[0].children[1].attribs.src,
              title: libox[i].children[0].children[4].children[1].attribs.title,
              last: libox[i].children[0].children[2].children[1].children[0].children[0] ? libox[i].children[0].children[2].children[1].children[0].children[0].data : '暂无',
              auto: libox[i].children[0].children[6].children[1].children[0] ? libox[i].children[0].children[6].children[1].children[0].data : '佚名'
            })

          }
        }
        this.axioscode = 200;
        setTimeout(() => {
          this.axioscode = 0;
        }, 1500)
        this.catarr[this.catindex].item = data;
      }).catch(err => {
        this.axioscode = 200;
        setTimeout(() => {
          this.axioscode = 0;
        }, 1000)
      });
      // this.$axios.get('qicomic/list', {id: this.catarr[this.catindex].href, num: 1}).then(res => {
      //   if (res.code === 200) {
      //     this.axioscode = 200;
      //     setTimeout(() => {
      //       this.axioscode = 0;
      //     }, 1500)
      //     this.catarr[this.catindex].item = res.data;
      //   }
      //
      // })
    },
    golist(item, index) {
      let data=[]
      this.loding = true;
      let arr = this.catarr;
      let box = document.querySelectorAll('.title-item');
      for (let i = 0; i < box.length; i++) {
        box[i].style.color = '#000';
      }
      box[index].style.color = '#F54882';
      if (!arr[index].item.length) {
        rp(`http://www.qiman6.com/${item.href}-1.html`).then(body => {
          let $ = cheerio.load(body);
          let libox = $('.bookList_3')[0].children;
          for (let i = 1; i < libox.length - 1; i++) {
            if (libox[i].children[1]) {
              data.push({
                id: libox[i].children[1].attribs.href,
                img: libox[i].children[2].children[0].children[1].attribs.src,
                title: libox[i].children[4].children[1].attribs.title,
                last: libox[i].children[2].children[1].children[0].children[0] ? libox[i].children[2].children[1].children[0].children[0].data : '暂无',
                auto: libox[i].children[6].children[1].children[0] ? libox[i].children[6].children[1].children[0].data : '佚名'
              })

