              data.push({
                id: libox[i].children[0].children[1].attribs.href,
                img: libox[i].children[0].children[2].children[0].children[1].attribs.src,
                title: libox[i].children[0].children[4].children[1].attribs.title,
                last: libox[i].children[0].children[2].children[1].children[0].children[0] ? libox[i].children[0].children[2].children[1].children[0].children[0].data : '暂无',
                auto: libox[i].children[0].children[6].children[1].children[0] ? libox[i].children[0].children[6].children[1].children[0].data : '佚名'
              })

            }
          }
          arr[index].item = data;
          this.loding = false;
        }).catch(err => {
          console.log(err);
        })
      } else {
        setTimeout(re => {
          this.loding = false;
        }, 200);
      }
      if (arr[index].item.length <= 80) {
        this.dolading = true;
        this.spilodin = false;
        this.haveno = false;
      } else {
        this.dolading = false;
        this.spilodin = false;
        this.haveno = true;
      }
      this.catindex = index;
    },
    //换个顺序
    itemlist(e, index) {

      let box2 = document.querySelectorAll('.serach-item');
      for (let i = 0; i < box2.length; i++) {
        box2[i].style.color = '#000';
      }
      e.toElement.style.color = '#F54882';
      this.catarr[this.catindex].item.reverse()
    },
