    let Url = `http://www.qiman6.com/sort/13-1.html`;
    let data = []
    rp(Url).then(body => {
      let $ = cheerio.load(body);
      let libox = $('.bookList_3')[0].children;
      for (let i = 1; i < libox.length - 1; i++) {
        if (libox[i].children[1]) {
          data.push({
            id: libox[i].children[1].attribs.href,
            img: libox[i].children[2].children[0].children[1].attribs.src,
            title: libox[i].children[4].children[1].attribs.title,
            last: libox[i].children[2].children[1].children[0].children[0] ? libox[i].children[2].children[1].children[0].children[0].data : '暂无',
            auto: libox[i].children[6].children[1].children[0] ? libox[i].children[6].children[1].children[0].data : '佚名'
          })

        } else {
          data.push({
            id: libox[i].children[0].children[1].attribs.href,
            img: libox[i].children[0].children[2].children[0].children[1].attribs.src,
            title: libox[i].children[0].children[4].children[1].attribs.title,
            last: libox[i].children[0].children[2].children[1].children[0].children[0] ? libox[i].children[0].children[2].children[1].children[0].children[0].data : '暂无',
            auto: libox[i].children[0].children[6].children[1].children[0] ? libox[i].children[0].children[6].children[1].children[0].data : '佚名'
          })

        }
      }
      this.catarr[0].item = data;
      this.loding = false
    }).catch(err => {
      console.log(err);
    })
    // this.$axios.get('qicomic/list', {id: 'sort/13', num: 1}).then(res => {
    //   if (res.code === 200) {
    //     this.catarr[0].item = res.data;
    //     this.loding = false
    //   }
    // });
    this.$nextTick(() => {
      document.querySelector('.title-item').style.color = '#F54882';
    })
  },
  activated() {
    mui.plusReady(function () {
      plus.navigator.setStatusBarStyle('dark');
    });
  },
  beforeRouteLeave(to, from, next) {
    this.$refs.catheizi.btscroll.destroy();
    next()
  },
